
import { useEffect, useState } from 'react';
import Colors from '../components/smallcomponents/Color';

function Homepage({ contentplaceholder }) {
    console.log(contentplaceholder)
    const [homepagecontent, setHomePageContent] = useState(styles.homespacestatemax);

    useEffect(() => {


        if (contentplaceholder === true) {
            setHomePageContent(styles.homespace_state_min)
        }
        else {
            setHomePageContent(styles.homespacestatemax)
        }


    }, [contentplaceholder])
    return (


        <div style={homepagecontent}>

            <div style={styles.cardbox}>
                <img style={styles.contentimage}
                    src="https://picsum.photos/215"
                    alt="display image" />
            </div>
            <div style={styles.cardbox}>
                <img
                    style={styles.contentimage}
                    src="https://picsum.photos/205"
                    alt="display image" />
            </div>
            <div style={styles.cardbox}>
                <img
                    style={styles.contentimage}
                    src="https://picsum.photos/204"
                    alt="display image" />
            </div>
            <div style={styles.cardbox}>
                <img
                    style={styles.contentimage}
                    src="https://picsum.photos/201"
                    alt="display image" />
            </div>
            <div style={styles.cardbox}>
                <img
                    style={styles.contentimage}
                    src="https://picsum.photos/206"
                    alt="display image" />
            </div>
            <div style={styles.cardbox}>
                <img
                    style={styles.contentimage}
                    src="https://picsum.photos/208"
                    alt="display image" />
            </div>
            <div style={styles.cardbox}>
                <img
                    style={styles.contentimage}
                    src="https://picsum.photos/275"
                    alt="display image" />
            </div>
            <div style={styles.cardbox}>
                <img
                    style={styles.contentimage}
                    src="https://www.creativefaze.com/sites/default/files/android-app-developer-Specialist111.jpg"

                    alt="display image" />
            </div>
        </div>



    );
}
const styles = ({
    homespace_state_min: {

        position: "static",
        marginTop: "100px",
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
        backgroundColor: Colors.homeContentcolor,
        justifyContent: "center",
        borderRadius: "20px"

    },
    homespacestatemax: {
        //this margin left is handeling the content ,remember this 
        marginLeft: "270px",
        marginRight: "3.5%",
        position: "static",
        marginTop: "100px",
        display: "flex",
        backgroundColor: Colors.homeContentcolor,
        justifyContent: "center",
        flexWrap: "wrap",
        flexDirection: "row",
        borderRadius: "20px"




    },

    cardbox: {
        height: "200px",
        width: "200px",
        backgroundColor: Colors.silver,
        borderRadius: "25px",
        margin: "10px",
    },
    contentimage: {
        width: "200px",
        height: "200px",
        borderRadius: "15px"


    },



})

export default Homepage;